import { configureStore } from '@reduxjs/toolkit'
import { createAction, createReducer, createSlice } from '@reduxjs/toolkit'
import contacts from "../Components/Contacts/contacts.json";


const contactsSlice = createSlice({
    name: 'contacts',
    initialState: {
        contacts: {
            items: [],
            filter: '',
        }
    },
    reducers: {
        addContact(state, action) {
            state.push(action.payload)
        },
        removeContact(state, action) {
            return state.filter((contact) => contact.id !== action.payload)
        },
        filterItems(state, action) {
            
        }
//     
    }
})

// add same for filter later !!!!

// export const { addContact, removeContact } = contactsSlice.actions;
// *******************
export const addContact = createAction('contacts/addContact')
export const removeContact = createAction('contacts/removeContact')
export const setFilter = createAction('filter/setFilter')

const contactsReducer = createReducer(contacts, {
    [addContact]: (state, action) => state.push(action.payload),
    [removeContact]: (state, action) => state.filter((contact) => contact.id !== action.payload),
})
const filterReducer = createReducer('', {
    [setFilter]: (state, action) => state.filter = action.payload
})

export const store = configureStore({
    reducer: {
        contacts: contactsReducer,
        filter: filterReducer,
  },
})